.wrap {
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
}

.wrap img {
  width: 80px;
  height: 80px;
  object-fit: cover;
  object-position: center;
}

.wrap h4 {
  font-weight: 700;
  font-size: 20px;
  line-height: 100%;
  letter-spacing: -0.03em;
  color: #f6b83d;
  margin: 20px 0;
}

.wrap p {
  font-size: 14px;
  line-height: 129%;
  letter-spacing: -0.02em;
  text-align: center;
  color: #2b2b2a;
  margin: 0;
  margin-bottom: 24px;
}

.wrapBtn {
  display: flex;
  gap: 8px;
}

.linkLogin {
  border-radius: 30px;
  display: flex;
  justify-content: center;
  align-items: center;

  width: 135px;
  height: 42px;
  background: #f6b83d;
  font-weight: 700;
  font-size: 14px;
  line-height: 129%;
  letter-spacing: -0.03em;
  color: #fff;
  text-decoration: none;
  cursor: pointer;
}

.linkReg {
  border-radius: 30px;

  width: 129px;
  height: 42px;
  background: #fff4df;
  font-weight: 700;
  font-size: 14px;
  line-height: 129%;
  letter-spacing: -0.03em;
  color: #f6b83d;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  cursor: pointer;
}
